<template>
    <div>
      <GridPdfExport ref="gridPdfExport">
        <Grid
          ref="grid"
          :style="{ height: '100%' }"
          :data-items="gridData"
          :resizable="true"
          :reorderable="true"
          :sortable="true"
          :pageable="gridPageable"
          :groupable="true"
          :group="group"
          :sort="sort"
          :filter="filter"
          :take="take"
          :skip="skip"
          :expand-field="expandField"
          :selectable="true"
          :selected-field="selectedField"
          :columns="columns"
          :column-menu="columnMenu"
          @selectionchange="onSelectionChange"
          @headerselectionchange="onHeaderSelectionChange"
          @datastatechange="dataStateChange"
          @expandchange="expandChange"
        >
          <GridToolbar>
            <span class="k-textbox k-grid-search k-display-flex ml-auto">
              <kinput
                :style="{ width: '230px' }"
                :placeholder="gridSearchMessage"
                :value="searchWord"
                @input="onFilter"
              ></kinput>
            </span>
            <span class="export-buttons">
              <kbutton
                title="Export to Excel"
                :theme-color="'primary'"
                @click="exportExcel"
              >
                Export Excel</kbutton
              >&nbsp;
              <kbutton :theme-color="'primary'" @click="exportPDF">
                Export PDF
              </kbutton>
            </span>
          </GridToolbar>
        </Grid>
      </GridPdfExport>
    </div>
  </template>
  
<script>
import { Grid, GridToolbar } from '@progress/kendo-vue-grid';
import { Input } from '@progress/kendo-vue-inputs';
import { Button } from '@progress/kendo-vue-buttons';
import { GridPdfExport } from '@progress/kendo-vue-pdf';
import { process } from '@progress/kendo-data-query';
import { saveExcel } from '@progress/kendo-vue-excel-export';

export default {
    components: {
      Grid,
      GridToolbar,
      GridPdfExport,
      kinput: Input,
      kbutton: Button,
    },
    inject: {
      kendoLocalizationService: { default: null },
    },
    data: function () {
      return {
        currentLocale: {
          language: 'English',
          locale: 'en',
        },
        localizationLanguage: 'English',
        searchWord: '',
        columnMenu: true,
        selectedField: 'selected',
        expandField: 'expanded',
        gridPageable: {
          buttonCount: 5,
          info: true,
          type: 'numeric',
          pageSizes: true,
          previousNext: true,
        },
        dataSet: [],
        gridData: [],
        skip: 0,
        take: 10,
        group: [],
        sort: [],
        filter: null,
        expandedItems: [],
      };
    },
    created: function () {
      this.gridData = this.getData(this.dataSet);
    },
    computed: {
      locale() {
        if (this.localizationLanguage === 'Spanish') {
          return 'es';
        }
        if (this.localizationLanguage === 'French') {
          return 'fr';
        }
        return 'en';
      },
      selectedDataItems() {
        return this.gridData.data.filter((item) => item.selected === true);
      },
      areAllSelected() {
        return (
          this.selectedDataItems.length === this.gridData.data.length && this.selectedDataItems.length !== 0
        );
      },
      columns() {
        return [
        {
            field: 'account',
            title: 'Account',
            width: '150px',
            columnMenu: true,
          },
          {
            field: 'amount',
            title: 'Amount',
            width: '150px',
            columnMenu: true,
            resizable: true,
            className: 'text-right',
            filter: 'numeric',
            format: '{0:c}',
          },
          {
            field: 'age',
            title: 'Age',
            width: '120px',
            columnMenu: true,
            resizable: true,
            className: 'text-right',
            filter: 'numeric',
          },
          {
            field: 'gender',
            title: 'Gender',
            width: '120px',
            columnMenu: true,
            resizable: true,
            className: 'text-center',
          },
          {
            field: 'effectdate',
            title: 'Effect Date',
            width: '200px',
            columnMenu: true,
            resizable: true,
            className: 'text-center',
          },
          {
            field: 'effecttime',
            title: 'Effect Time',
            width: '150px',
            columnMenu: true,
            resizable: true,
            className: 'text-center',
          },
          {
            field: 'title',
            title: 'Title',
            width: '350px',
            columnMenu: true,
            resizable: true,
          },
        ];
      },
    },
    methods: {
        reset(newData=[]) {
            this.dataSet = newData;
            this.gridData = this.getData(newData);
        },
        clear() {
          this.reset();
        },
      onHeaderSelectionChange(event) {
        let checked = event.event.target.checked;
        const tempData = this.dataSet.map((item) => {
          return { ...item, selected: checked };
        });
  
        const tempGridData = this.gridData.data.map((item) => {
          return { ...item, selected: checked };
        });
        this.gridData.data = tempGridData;
        this.dataSet = tempData;
      },
      getData(data) {
        return process(data, {
          take: this.take,
          skip: this.skip,
          group: this.group,
          sort: this.sort,
          filter: this.filter,
        });
      },
      createAppState: function (dataState) {
        this.group = dataState.group;
        this.take = dataState.take;
        this.skip = dataState.skip;
        this.sort = dataState.sort;
        this.filter = dataState.filter;
        this.gridData = this.getData(this.dataSet);
      },
      dataStateChange: function (event) {
        this.createAppState(event.data);
      },
      expandChange: function (event) {
        if (event.dataItem) {
          event.dataItem[event.target.$props.expandField] = event.value;
        }
      },
      exportExcel() {
        const columnsToExport = [...this.columns];
        columnsToExport.splice(0, 1);
        saveExcel({
          data: this.gridData.data,
          group: this.group,
          fileName: 'DataSet',
          columns: columnsToExport,
        });
      },
      exportPDF() {
        const tempSort = this.sort;
        this.sort = null;
        this.$nextTick(() => {
          this.columnMenu = false;
          this.$refs.gridPdfExport.save(
            process(this.gridData.data, { skip: this.skip, take: this.take })
          );
          this.columnMenu = true;
          this.sort = tempSort;
        });
      },
      onSelectionChange(event) {
        event.dataItem[this.selectedField] = !event.dataItem[this.selectedField];
      },
    },
};
</script>
  